<template>
  <div class="jumbotron">
    <h1 v-if="title" class="display-4">{{title}}</h1>
    <p class="lead">{{text}}</p>
    <hr class="my-4">
    <p v-if="subtitle">{{subtitle}}</p>
    <p class="lead">
      <slot />
    </p>
  </div>
</template>

<script>
export default {
  name: 'Jumbotron',
  props: {
    title: String,
    subtitle: String,
    text: String
  }
}
</script>



